<template>
  <div class="login_box">
    <span class="title">杭州黑马教学课堂系统</span>
    <div class="center_box">
      <el-form ref="form" :rules="formRules" :model="form">
        <el-form-item prop="name">
          <el-input name="mobile" @blur="usernameBlur" placeholder="请输入账号" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input type="password" placeholder="请输入密码" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item v-if="isShowClass" prop="classId">
          <el-select style="width: 100%" v-model="form.classId" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button style="width: 100%" @click="onLogin" type="primary">登录</el-button>
          <p>如忘记密码，请联系当前班级导师</p>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { queryUserRoles, userLogin } from '../../api/user.js'
export default {
  data() {
    // 校验手机号是否合法
    // const checkMobile = (rule, value, cb) => {
    //   const mobileReg = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/
    //   if (mobileReg.test(value)) {
    //     return cb()
    //   }
    //   return cb(new Error('手机号码格式不正确'))
    // }
    return {
      form: {
        name: '', // 13012345678: 导师 13022224444: 学员
        password: '',
        classId: ''
      },
      isShowClass: false,
      formRules: {
        name: [
          { required: true, message: '请输入账号', trigger: 'blur' }
          // { validator: checkMobile, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '密码在 6 到 15 个字符', trigger: 'blur' }
        ],
        classId: [
          { required: true, message: '请选择班级', trigger: 'change' }
        ]
      },
      options: [
      ]
    }
  },

  created() {

  },

  methods: {
    // 登录
    async onLogin() {
      try {
        await this.$refs.form.validate()
        if (this.isShowClass) {
          // 测试班级id
          // this.form.classId = 1
          const { data: res } = await userLogin(this.form)
          console.log(res)
          if (res.code !== 0) return this.$message(res.errorMessage)
          this.$store.commit('setClassId', this.form.classId)
          this.$message.success('登录成功')
          localStorage.setItem('hmhz-intergral-token', res.data)
          this.$router.push('/home')
        } else {
          const { data: res } = await userLogin({
            name: this.form.name,
            password: this.form.password
          })
          if (res.code !== 0) return this.$message('登录失败')
          this.$message.success('登录成功')
          localStorage.setItem('hmhz-intergral-token', res.data)
          this.$router.push('/students/home')
        }
      } catch (err) { }
    },
    // 用户名输入框失去焦点
    usernameBlur() {
      this.$refs.form.validateField('name', async valid => {
        console.log(valid)
        if (valid !== '') return
        const { data: res } = await queryUserRoles(this.form.name)
        if (res.data === '学生账户' || res.errorMessage === '用户不存在') {
          this.isShowClass = false
          return
        }
        this.isShowClass = true
        this.options = res.data
      })
    }
  }
}
</script>

<style scoped lang='less'>
.login_box {
  width: 100%;
  height: 100%;
  background-color: #e9eef3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .title {
    font-size: 20px;
    margin-bottom: 15px;
  }
  .center_box {
    background-color: #b3c0d1;
    width: 400px;
    // padding-top: 50px;
    padding: 50px 40px 0px 40px;
    box-sizing: border-box;
    border-radius: 10px;
  }
}
</style>
